body,
dir {
    margin: 0;
    padding: 0;
}

#root {
    width: 100vw;
    height: 100vh;
}

.p-0 {
    padding: 0;
}

.p-1 {
    padding: 1rem;
}

.m-0 {
    margin: 0;
}

.h-100 {
    height: 100%;
}

.w-100 {
    width: 100%;
}

.flex-row {
    display: flex;
}

.flex-column {
    display: flex;
    flex-direction: column;
}

.f-1 {
    flex: 1;
}

.left-panel {
    flex: 0 0 200px;
}

.task-page-container {
    justify-content: space-between;
}

.task-box {
    border: 1px solid gray;
    flex-grow: 1;
    justify-content: space-between;
}
